.leftbar {
    flex: 3;
    height: calc(100vh - 50px);
    overflow-y: scroll;
    position: sticky;
    top: 50px;
  }
  
  ::-webkit-scrollbar {
    width: 6px;
  }
  ::-webkit-scrollbar-track {
    background-color: #f1f1f1 ;
  }
  ::-webkit-scrollbar-thumb {
    background-color:  rgb(179, 179, 179);
  }
 
  
  .leftwrapper {
    padding: 20px;
  }
  
  .leftbarlist {
    padding: 0;
    margin: 0;
    list-style: none;
  }
  
  .leftbarlistitem {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .leftbaricon {
    margin-right: 15px;
  }
  
  .sidebarButton {
    width: 150px;
    border: none;
    padding: 10px;
    border-radius: 5px;
    font-weight: 500;
  }

  .sidebarHr {
    margin: 20px 0;
  }
  
  .sidebarFriendList {
    padding: 0;
    margin: 0;
    list-style: none;
  }

  .sidebarFriendList{
    padding: 0;
    margin: 0;
    list-style: none;
  }

  .sidebarFriend{
    display: flex;
    align-items: center;
    margin-bottom: 15px;
  }

  .sidebarFriendImg{
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
  }